$color: #666;

@font-face {
  font-family: "SegoeUI";
  src: url("../fonts/segoeui.eot"); /* IE9 Compat Modes */
  src: url("../fonts/segoeui.otf") format("opentype"), /* Open Type Font */
  url("../fonts/segoeui.svg") format("svg"), /* Legacy iOS */
  url("../fonts/segoeui.ttf") format("truetype"), /* Safari, Android, iOS */
  url("../fonts/segoeui.woff") format("woff"); /* Modern Browsers */
  font-weight: normal;
  font-style: normal;
}

/* Media Querry */

@mixin media-querry($point) {
  @if $point == desktop {
    @media only screen and (min-width: 1024px){ @content ; }
  }
  @else if $point == smaller-desktop {
    @media only screen and (max-width: 1023px) { @content ; }
  }
  @else if $point == tablet {
    @media only screen and (min-width: 768px) and (max-width: 1023px) { @content ; }
  }
  @else if $point == phone {
    @media (max-width: 767px) { @content ; }
  }
}


